.secondPage {
  width: 100%; }

.section {
  height: 100%; }

@-webkit-keyframes changeWidth {
  0% {
    opacity: 0.1; }
  50% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

@keyframes changeWidth {
  0% {
    opacity: 0.1; }
  50% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

.invisible {
  opacity: 0; }

.visible {
  -webkit-animation: changeWidth 1s linear;
          animation: changeWidth 1s linear; }

.boxFirst,
.boxSecond {
  width: 80%;
  margin: 1rem auto 0 auto;
  background: #4d3210; }
  @media screen and (min-width: 768px) {
    .boxFirst,
    .boxSecond {
      width: 50%;
      height: 90%; } }
  @media screen and (min-width: 1400px) {
    .boxFirst,
    .boxSecond {
      width: 30%; } }

.descriptionFirst,
.descriptionSecond {
  font-size: 1.4rem;
  padding: 1rem;
  word-wrap: break-word;
  position: relative;
  top: -1rem;
  text-align: center;
  opacity: 0;
  -webkit-transform: rotateX(-90deg);
          transform: rotateX(-90deg);
  -webkit-transition: all 1s linear;
  -o-transition: all 1s linear;
  transition: all 1s linear;
  -webkit-transition-delay: 300ms;
       -o-transition-delay: 300ms;
          transition-delay: 300ms; }

.nextDescription {
  opacity: 1;
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg); }

.photoFirst,
.photoSecond {
  width: 100%;
  height: auto;
  margin: 0 auto; }

@-webkit-keyframes transBelt {
  0% {
    width: 0%;
    z-index: 10; }
  50% {
    width: 80%;
    opacity: 1; }
  80% {
    width: 100%;
    opacity: 0.5; }
  100% {
    width: 100%;
    opacity: 0; } }

@keyframes transBelt {
  0% {
    width: 0%;
    z-index: 10; }
  50% {
    width: 80%;
    opacity: 1; }
  80% {
    width: 100%;
    opacity: 0.5; }
  100% {
    width: 100%;
    opacity: 0; } }

.belt {
  display: block;
  position: relative;
  top: 2.5rem;
  height: 12px;
  border-radius: 10px;
  opacity: 0;
  background: -webkit-gradient(linear, right top, left top, from(rgba(199, 113, 113, 0.8)), to(rgba(121, 223, 193, 0)));
  background: -webkit-linear-gradient(right, rgba(199, 113, 113, 0.8), rgba(121, 223, 193, 0));
  background: -o-linear-gradient(right, rgba(199, 113, 113, 0.8), rgba(121, 223, 193, 0));
  background: linear-gradient(to left, rgba(199, 113, 113, 0.8), rgba(121, 223, 193, 0));
  -webkit-animation: transBelt 1.5s linear;
          animation: transBelt 1.5s linear;
  z-index: 10; }
  @media screen and (min-width: 768px) {
    .belt {
      top: 4.5rem; } }

.subTitle {
  font-size: 2rem;
  margin: 0;
  text-align: center;
  position: relative;
  top: 1rem;
  color: #202020;
  text-shadow: 2px 2px 8px #ffffff;
  z-index: 30; }
  @media screen and (min-width: 768px) {
    .subTitle {
      font-size: 3rem; } }



